import React, { useState, useEffect } from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
//eslint-disable-next-line
import { isEmpty, size } from 'lodash';
//eslint-disable-next-line
import { addDocument, deleteDocument, getCollection, updateDocument } from './actions'




function App() {
    const [nom, setNombre] = useState("")
    const [cor, setCorreo] = useState("")
    const [tel, setTelefono] = useState("")
    const [desc, setDescricpion] = useState("")
    const [tasks, setTasks] = useState([])
    const [editMode, setEditMode] = useState(false)
    const [id, setId] = useState("")
    //eslint-disable-next-line
    const [error, setError] = useState(null)


    useEffect(() => {
        (async () => {
            const result = await getCollection("clientes")
            console.log(result)
            if (result.statusResponse) {
                setTasks(result.data)
            }
        })()
    }, [])


    const addTask = async (e) => {
        e.preventDefault()
        if (isEmpty(nom)) {
            console.log("Cliente vacío")
            return
        }

        if (isEmpty(cor)) {
            console.log("Cliente vacío")
            return
        }

        if (isEmpty(tel)) {
            console.log("Cliente vacío")
            return
        }

        if (isEmpty(desc)) {
            console.log("Cliente vacío")
            return
        }


        const result = await addDocument("clientes", { nombre: nom, correo: cor, telefono: tel, descripcion: desc })
        if (!result.statusResponse) {
            setError(result.error)
            return
        }


        /*  const newTask={
            id:shortid.generate(),
            name:task
            }*/

        setTasks([...tasks, { id: result.data.id, nombre: nom, correo: cor, telefono: tel, descripcion: desc }])
        setNombre("")
        setCorreo("")
        setTelefono("")
        setDescricpion("")
    }

    const saveTask = async (e) => {
        e.preventDefault()
        if (isEmpty(nom)) {
            console.log("Cliente vacío")
            return
        }

        if (isEmpty(cor)) {
            console.log("Cliente vacío")
            return
        }

        if (isEmpty(tel)) {
            console.log("Cliente vacío")
            return
        }

        if (isEmpty(desc)) {
            console.log("Cliente vacío")
            return
        }

        const result = await updateDocument("clientes", id, { nombre: nom, correo: cor, telefono: tel, descripcion: desc })
        if (!result.statusResponse) {
            setError(result.error)
            return
        }

        const editedTasks = tasks.map(item => item.id === id ? { id, nombre: nom, correo: cor, telefono: tel, descripcion: desc } : item)
        setTasks(editedTasks)
        setEditMode(false)
        setNombre("")
        setCorreo("")
        setTelefono("")
        setDescricpion("")
        setId("")
    }
//eslint-disable-next-line
    const deleteTask = async (id) => {
        const result = await deleteDocument("clientes", id)
        if (!result.statusResponse) {
            setError(result.error)
            return
        }
        const filteredTasks = tasks.filter(task => task.id !== id)
        setTasks(filteredTasks)
    }

// eslint-disable-next-line
    const editTask = (tarea) => {
        setNombre(tarea.nombre)
        setCorreo(tarea.correo)
        setTelefono(tarea.telefono)
        setDescricpion(tarea.descripcion)
        setEditMode(true)
        setId(tarea.id)
    }

    return (
        <>
                <div className="container mt-4">
                    <div className="row align-items-start ">
                        <div className="col-6  container-fluid">
                            <h4 className="text-center navbar navbar-warning bg-warning text-light">{editMode ? "Modificar problema" : "Inserta la Problemática"}</h4>
                            <form onSubmit={editMode ? saveTask : addTask}>
                                <label>Nombre</label>
                                <input
                                    type="text"
                                    className="form-control mb-2"
                                    placeholder="Agrega un Nombre..."
                                    onChange={(text) => setNombre(text.target.value)}
                                    value={nom.name}
                                />

                                <label>Correo</label>
                                <input
                                    type="text"
                                    className="form-control mb-2"
                                    placeholder="Agregar el Correo..."
                                    onChange={(text) => setCorreo(text.target.value)}
                                    value={cor.correo}
                                />

                                <label>Teléfono</label>
                                <input
                                    type="num"
                                    className="form-control mb-2"
                                    placeholder="Agrega un Teléfono..."
                                    onChange={(text) => setTelefono(text.target.value)}
                                    value={tel.telefono}
                                />

                                <label>Descripción</label>
                                <input
                                    type="text"
                                    className="form-control mb-2"
                                    placeholder="Agrega la Descripción..."
                                    onChange={(text) => setDescricpion(text.target.value)}
                                    value={desc.descripcion}
                                />


                                <button className={editMode ? "btn btn-warning btn-block" : "btn btn-warning btn-block"}
                                    type="submit"
                                > {editMode ? "Guardar" : "Guardar"}</button>
                                
                            </form>
                        </div>
                    </div>
                </div>
                 
        </>
    )
}

export default App;
